import {CHECKBOX, RADIO, REPEATER, SELECT, SELECT_MULTI} from "../../constants/fields";

class MetaHotKeys
{
    /**
     *
     * @param selectedElement
     * @param boxes
     * @param fields
     * @param values
     */
    constructor(selectedElement, blocks, boxes, fields, values)
    {
        this.selectedElement = selectedElement;
        this.blocks = blocks;
        this.boxes = boxes;
        this.values = values;
        this.fields = fields;
    }

    /**
     *
     * @return {string[]}
     */
    keys = () => {
        return [
            'esc',
            'escape',
            'tab',
            'up',
            'down',
            'left',
            'right',
            'control+left',
            'command+left',
            'control+right',
            'command+right',
            'control+A',
            'command+A',
            'control+B',
            'command+B',
            'control+C',
            'command+C',
            'control+D',
            'command+D',
            'control+F',
            'command+F',
            'control+L',
            'command+L',
            'control+O',
            'command+O',
            'control+R',
            'command+R',
            'control+S',
            'command+S',
            'control+del',
            'command+del',
            'control+shift+A',
            'command+shift+A',
            'control+shift+B',
            'command+shift+B',
            'control+shift+R',
            'command+shift+R',
            'control+V',
            'command+V',
            'control+shift+del',
            'command+shift+del',
            'control+shift+V',
            'command+shift+V',
        ];
    };

    /**
     *
     * @return {*}
     */
    keysMap = () => {
        return this.keys().join(',');
    };

    /**
     *
     * @return {{canHaveOptions: boolean, isBlock: boolean, visibilityConditionId: null, hasChildren: boolean, isField: boolean, isOption: boolean, box: null, isVisibilityCondition: boolean, parentFieldId: null, blockId: null, field: null, isSaved: boolean, optionId: null, block: null, isBox: boolean, isChild: boolean, boxId: null, fieldId: null}}
     */
    selectedElementDetails = () => {

        let object = {
            isBox: false,
            isField: false,
            hasChildren: false,
            isOption: false,
            isVisibilityCondition: false,
            isChild: false,
            isBlock: false,
            canHaveOptions: false,
            isSaved: false,
            box: null,
            boxId: null,
            parentFieldId: null,
            field: null,
            fieldId: null,
            optionId: null,
            block: null,
            blockId: null,
            visibilityConditionId: null
        };

        if(this.selectedElement){
            this.values.forEach(box => {
                if(box.id === this.selectedElement){

                    const selectedBox = this.boxes.filter(b => b.props.id === this.selectedElement)[0];

                    object = {
                        isBox: true,
                        isField: false,
                        hasChildren: false,
                        isOption: false,
                        isVisibilityCondition: false,
                        isChild: false,
                        isBlock: false,
                        canHaveOptions: false,
                        isSaved: selectedBox.props.isSaved,
                        box: box,
                        boxId: box.id,
                        parentFieldId: null,
                        field: null,
                        fieldId: null,
                        optionId: null,
                        block: null,
                        blockId: null,
                        visibilityConditionId: null
                    };

                    return;
                }

                box.fields && box.fields.forEach(field => {

                    const canHaveOptions = (field.type === SELECT || field.type === SELECT_MULTI || field.type === RADIO || field.type === CHECKBOX);
                    const hasChildren = (field.type === REPEATER);

                    if(field.id === this.selectedElement){

                        const selectedField = this.fields.filter(f => f.props.id === this.selectedElement)[0];

                        object = {
                            isBox: false,
                            isField: true,
                            hasChildren: hasChildren,
                            isOption: false,
                            isVisibilityCondition: false,
                            isChild: false,
                            isBlock: false,
                            canHaveOptions: canHaveOptions,
                            isSaved: selectedField.props.isSaved,
                            box: box,
                            boxId: box.id,
                            parentFieldId: null,
                            field: field,
                            fieldId: field.id,
                            optionId: null,
                            block: null,
                            blockId: null,
                            visibilityConditionId: null
                        };

                        return;
                    }

                    field.visibilityConditions && field.visibilityConditions.forEach(condition => {
                        if(condition.id === this.selectedElement){
                            object = {
                                isBox: false,
                                isField: false,
                                hasChildren: false,
                                isOption: false,
                                isVisibilityCondition: true,
                                isChild: false,
                                isBlock: false,
                                canHaveOptions: true,
                                isSaved: false,
                                box: box,
                                boxId: box.id,
                                parentFieldId: null,
                                field: field,
                                fieldId: field.id,
                                optionId: null,
                                block: null,
                                blockId: null,
                                visibilityConditionId: condition.id
                            };
                        }

                        return;
                    });

                    canHaveOptions && field.options && field.options.forEach(option => {
                        if(option.id === this.selectedElement){
                            object = {
                                isBox: false,
                                isField: false,
                                hasChildren: false,
                                isOption: true,
                                isVisibilityCondition: false,
                                isChild: false,
                                isBlock: false,
                                canHaveOptions: true,
                                isSaved: false,
                                box: box,
                                boxId: box.id,
                                parentFieldId: null,
                                field: field,
                                fieldId: field.id,
                                optionId: option.id,
                                block: null,
                                blockId: null,
                                visibilityConditionId: null
                            };
                        }

                        return;
                    });

                    field.blocks && field.blocks.forEach(block => {

                        if(block.id === this.selectedElement){

                            const selectedBlock = this.blocks.filter(f => f.props.id === this.selectedElement)[0];

                            object = {
                                isBox: false,
                                isField: false,
                                hasChildren: false,
                                isOption: false,
                                isVisibilityCondition: false,
                                isChild: false,
                                isBlock: true,
                                canHaveOptions: false,
                                isSaved: selectedBlock.props.isSaved,
                                box: box,
                                boxId: box.id,
                                parentFieldId: null,
                                field: field,
                                fieldId: field.id,
                                optionId: null,
                                block: block,
                                blockId: block.id,
                                visibilityConditionId: null
                            };
                        }

                        block.fields && block.fields.forEach(nestedField => {

                            const canHaveOptions = (nestedField.type === SELECT || nestedField.type === SELECT_MULTI || nestedField.type === RADIO || nestedField.type === CHECKBOX);

                            if(nestedField.id === this.selectedElement){
                                const selectedField = this.fields.filter(f => f.props.id === this.selectedElement)[0];

                                object = {
                                    isBox: false,
                                    isField: true,
                                    hasChildren: false,
                                    isOption: false,
                                    isVisibilityCondition: false,
                                    isChild: false,
                                    isBlock: false,
                                    canHaveOptions: canHaveOptions,
                                    isSaved: selectedField.props.isSaved,
                                    box: box,
                                    boxId: box.id,
                                    parentFieldId: null,
                                    field: nestedField,
                                    fieldId: nestedField.id,
                                    optionId: null,
                                    block: null,
                                    blockId: null,
                                    visibilityConditionId: null
                                };

                                return;
                            }

                            nestedField.visibilityConditions && nestedField.visibilityConditions.forEach(condition => {
                                if(condition.id === this.selectedElement){
                                    object = {
                                        isBox: false,
                                        isField: false,
                                        hasChildren: false,
                                        isOption: false,
                                        isVisibilityCondition: true,
                                        isChild: false,
                                        isBlock: false,
                                        canHaveOptions: false,
                                        isSaved: false,
                                        box: box,
                                        boxId: box.id,
                                        parentFieldId: field.id,
                                        field: nestedField,
                                        fieldId: nestedField.id,
                                        optionId: null,
                                        block: null,
                                        blockId: null,
                                        visibilityConditionId: condition.id
                                    };
                                }

                                return;
                            });

                            canHaveOptions && nestedField.options && nestedField.options.forEach(option => {
                                if(option.id === this.selectedElement){
                                    object = {
                                        isBox: false,
                                        isField: false,
                                        hasChildren: false,
                                        isOption: true,
                                        isVisibilityCondition: false,
                                        isChild: false,
                                        isBlock: false,
                                        canHaveOptions: false,
                                        isSaved: false,
                                        box: box,
                                        boxId: box.id,
                                        parentFieldId: field.id,
                                        field: nestedField,
                                        fieldId: nestedField.id,
                                        optionId: option.id,
                                        block: null,
                                        blockId: null,
                                        visibilityConditionId: null
                                    };
                                }

                                return;
                            });
                        });

                        return;
                    });

                    field.children && field.children.forEach(child => {

                        const canHaveOptions = (field.type === SELECT || field.type === SELECT_MULTI || field.type === RADIO || field.type === CHECKBOX);

                        if(child.id === this.selectedElement){

                            const selectedField = this.fields.filter(f => f.props.id === this.selectedElement)[0];

                            object = {
                                isBox: false,
                                isField: true,
                                hasChildren: false,
                                isOption: false,
                                isVisibilityCondition: false,
                                isChild: true,
                                isBlock: false,
                                canHaveOptions: canHaveOptions,
                                isSaved: selectedField.props.isSaved,
                                box: box,
                                boxId: box.id,
                                parentFieldId: field.id,
                                field: child,
                                fieldId: child.id,
                                optionId: null,
                                block: null,
                                blockId: null,
                                visibilityConditionId: null
                            };

                            return;
                        }

                        child.visibilityConditions && child.visibilityConditions.forEach(condition => {
                            if(condition.id === this.selectedElement){
                                object = {
                                    isBox: false,
                                    isField: false,
                                    hasChildren: false,
                                    isOption: false,
                                    isVisibilityCondition: true,
                                    isChild: false,
                                    isBlock: false,
                                    canHaveOptions: false,
                                    isSaved: false,
                                    box: box,
                                    boxId: box.id,
                                    parentFieldId: field.id,
                                    field: child,
                                    fieldId: child.id,
                                    optionId: null,
                                    block: null,
                                    blockId: null,
                                    visibilityConditionId: condition.id
                                };
                            }

                            return;
                        });

                        canHaveOptions && child.options && child.options.forEach(option => {
                            if(option.id === this.selectedElement){
                                object = {
                                    isBox: false,
                                    isField: false,
                                    hasChildren: false,
                                    isOption: true,
                                    isVisibilityCondition: false,
                                    isChild: false,
                                    isBlock: false,
                                    canHaveOptions: false,
                                    isSaved: false,
                                    box: box,
                                    boxId: box.id,
                                    parentFieldId: field.id,
                                    field: child,
                                    fieldId: child.id,
                                    optionId: option.id,
                                    block: null,
                                    blockId: null,
                                    visibilityConditionId: null
                                };
                            }

                            return;
                        });
                    });
                });
            });
        }

        return object;
    };

    /**
     *
     * @return {[*, *]}
     */
    prevNextElements = () => {

        let prev, next;
        let ids = [];

        this.values.forEach(box => {
            ids.push(box.id);

            box.fields && box.fields.forEach(field => {
                ids.push(field.id);

                field.options && field.options.forEach(option => {
                    ids.push(option.id);
                });

                field.visibilityConditions && field.visibilityConditions.forEach(condition => {
                    ids.push(condition.id);
                });

                field.blocks && field.blocks.forEach(block => {
                    ids.push(block.id);

                    block.fields && block.fields.forEach(nestedField => {
                        ids.push(nestedField.id);
                    });
                });
            });
        });

        const idsCount = ids.length;

        if(this.selectedElement === null){
            prev = ids[0];
            next = ids[idsCount-1];
        } else {
            const index = ids.indexOf(this.selectedElement);
            prev = ids[index-1] ? ids[index-1] : ids[idsCount-1];
            next = ids[index+1] ? ids[index+1] : ids[0];
        }

        return [prev, next];
    };
}

export default MetaHotKeys;