import React, {useEffect, useState} from 'react';
import MiniNavMap from "../../reusable/MiniNavMap";
import Hotkeys from 'react-hot-keys';
import {useDispatch, useSelector} from "react-redux";
import {
    changeAccordionFieldTab,
    createBox,
    createField, createFieldBlock,
    createOption,
    createVisibilityCondition,
    deleteBox,
    deleteField, deleteFieldBlock,
    deleteOption,
    deleteVisibilityCondition,
    deselectElements,
    duplicateMetaBox,
    duplicateMetaField, duplicateMetaFieldBlock,
    selectElement, setCurrentFieldTab,
    setStatusSaved,
    showHideAll,
    switchView,
    toggleFieldIsRequired,
    toggleFieldShowInArchive
} from "../../../redux/actions/metaStateActions";
import {metaSubmit} from "../../../redux/thunks/metaSubmit";
import {deleteAllMeta} from "../../../redux/thunks/deleteAllMeta";
import {scrollToId} from "../../../utils/scroll";
import { FLEXIBLE} from "../../../constants/fields";
import CopyMetaBoxModal from "./MetaBox/Modal/CopyMetaBoxModal";
import CopyMetaFieldModal from "./MetaBoxField/Modal/CopyMetaFieldModal";
import KeyboardLegendModal from "./KeyboardLegendModal";
import {Icon} from "@iconify/react";
import CopyMetaBlockModal from "./MetaBoxField/MetaBlock/Modal/CopyMetaBlockModal";
import useTranslation from "../../../hooks/useTranslation";
import {VerticalSortableList} from "../Sortable/VerticalSortableList";
import MetaHotKeys from "../../../redux/helpers/metaHotKeys";
import Tippy from "../Tippy";

const Meta = ({id, belongsTo, find, boxes, fields, blocks, onSortEnd, values}) => {

    // manage global state
    const dispatch = useDispatch();
    const {fetched: fetchedMeta} = useSelector(state => state.fetchMetaReducer);
    const {selectedElement, view} = useSelector(state => state.metaStateReducer);

    // manage local state
    const [copyBoxModalVisible, setCopyBoxModalVisible] = useState(false);
    const [copyFieldModalVisible, setCopyFieldModalVisible] = useState(false);
    const [copyBlockModalVisible, setCopyBlockModalVisible] = useState(false);
    const [legendModalVisible, setLegendModalVisible] = useState(false);
    const [modalsContent, setModalContent] = useState(null);

    // show hide all fields handlers
    const showHideAllFields = () => {
        dispatch(showHideAll());
    };

    // switch view handler
    const handleSwitchView = (view) => {
        localStorage.setItem(`META_SWITCH_VIEW_${id}`, view);
        dispatch(switchView(view));
    };

    // set saved switch view
    useEffect(() => {

        // get the default view
        const getDefaultView = () => {
            if(localStorage.getItem(`META_SWITCH_VIEW_${id}`)){
                return localStorage.getItem(`META_SWITCH_VIEW_${id}`);
            }

            return 'list';
        };

        dispatch(switchView(getDefaultView()));
    }, [id]);

    // manage key interactions
    const metaHotKeys = new MetaHotKeys(selectedElement, blocks, boxes, fields, values);
    const [prev, next] = metaHotKeys.prevNextElements();

    /**
     * Handle key press event
     *
     * @param key
     * @param e
     */
    const handleKeyPressEvent = (key, e) => {
        e.preventDefault();

        const {
            isBox,
            isField,
            hasChildren,
            isOption,
            isBlock,
            isVisibilityCondition,
            canHaveOptions,
            boxId,
            box,
            fieldId,
            field,
            optionId,
            visibilityConditionId,
            block,
            blockId,
            isSaved
        } = metaHotKeys.selectedElementDetails();

        switch (key) {

            case "esc":
            case "escape":
                dispatch(deselectElements());
                setCopyBoxModalVisible(false);
                setCopyFieldModalVisible(false);
                setLegendModalVisible(false);
                setModalContent(null);
                break;

            case "tab":
                if(isField || isOption){
                    const firstElement = document.getElementById(selectedElement).querySelector('input[type=text]').id;
                    document.getElementById(firstElement).focus();
                }

                break;

            case "up":
                dispatch(selectElement(prev));
                scrollToId(prev, -230);

                const prevField = fields.filter((f) => f.props.id === prev);

                if(prevField.length === 1){
                    dispatch(setCurrentFieldTab(
                        prevField[0].props.boxId,
                        prevField[0].props.id,
                        prevField[0].props.parentId ? prevField[0].props.parentId : null,
                        prevField[0].props.blockId ? prevField[0].props.blockId : null
                    ));

                    if(view === 'tabular' && !prevField[0].props.parentId && !prevField[0].props.blockId){
                        scrollToId(prevField[0].props.boxId,-230);
                    }
                }

                break;

            case "down":
                dispatch(selectElement(next));
                scrollToId(next,-230);

                const nextField = fields.filter((f) => f.props.id === next);

                if(nextField.length === 1){
                    dispatch(setCurrentFieldTab(
                        nextField[0].props.boxId,
                        nextField[0].props.id,
                        nextField[0].props.parentId ? nextField[0].props.parentId : null,
                        nextField[0].props.blockId ? nextField[0].props.blockId : null
                    ));

                    if(view === 'tabular' && !nextField[0].props.parentId && !nextField[0].props.blockId){
                        scrollToId(nextField[0].props.boxId,-230);
                    }
                }

                break;

            case 'left':
                if(view === 'tabular' && isField){

                    const currentFieldIndex = fields.findIndex(f => f.props.id === fieldId);

                    if(typeof fields[currentFieldIndex-1] !== 'undefined'){
                        dispatch(selectElement(fields[currentFieldIndex-1].props.id));
                        dispatch(setCurrentFieldTab(
                            fields[currentFieldIndex-1].props.boxId,
                            fields[currentFieldIndex-1].props.id,
                            fields[currentFieldIndex-1].props.parentId ? fields[currentFieldIndex-1].props.parentId : null,
                            fields[currentFieldIndex-1].props.blockId ? fields[currentFieldIndex-1].props.blockId : null
                        ));
                    }
                }

                break;

            case 'right':
                if(view === 'tabular' && isField){

                    const currentFieldIndex = fields.findIndex(f => f.props.id === fieldId);

                    if(typeof fields[currentFieldIndex+1] !== 'undefined'){
                        dispatch(selectElement(fields[currentFieldIndex+1].props.id));
                        dispatch(setCurrentFieldTab(
                            fields[currentFieldIndex+1].props.boxId,
                            fields[currentFieldIndex+1].props.id,
                            fields[currentFieldIndex+1].props.parentId ? fields[currentFieldIndex+1].props.parentId : null,
                            fields[currentFieldIndex+1].props.blockId ? fields[currentFieldIndex+1].props.blockId : null
                        ));
                    }
                }

                break;

            case 'control+left':
            case "command+left":
                if(isField){
                    let activeTab = field.activeTab ? field.activeTab - 1 : 0;

                    if(activeTab < 0){
                        activeTab = 0;
                    }

                    dispatch(changeAccordionFieldTab(boxId, fieldId, activeTab));

                }

                break;

            case 'control+right':
            case "command+right":
                if(isField){

                    let activeTab = field.activeTab ? field.activeTab + 1 : 1;

                    if(activeTab > 2){
                        activeTab = 2;
                    }

                    dispatch(changeAccordionFieldTab(boxId, fieldId, activeTab));
                }

                break;

            case "control+A":
            case "command+A":
                if(isField){
                    dispatch(toggleFieldShowInArchive(fieldId, boxId, true));
                }

                break;

            case "control+B":
            case "command+B":
                dispatch(createBox(belongsTo, find));

                break;

            case "control+C":
            case "command+C":
                if(isBox && isSaved){
                    setCopyBoxModalVisible(true);
                    setModalContent(<CopyMetaBoxModal box={box} modalVisible={true} setModalVisible={setCopyBoxModalVisible} />);
                } else if (isField && isSaved){
                    setCopyFieldModalVisible(true);
                    setModalContent(<CopyMetaFieldModal field={field} belongsTo={belongsTo} modalVisible={true} setModalVisible={setCopyFieldModalVisible} />);
                } else if (isBlock && isSaved) {
                    setCopyBlockModalVisible(true);
                    setModalContent(<CopyMetaBlockModal box={box} field={field} block={block} modalVisible={true} setModalVisible={setCopyBlockModalVisible} />);
                }

                break;

            case "control+D":
            case "command+D":
                if(isBox){
                    dispatch(duplicateMetaBox(boxId));
                    dispatch(setStatusSaved());
                } else if (isField){
                    dispatch(duplicateMetaField(boxId, fieldId));
                    dispatch(setStatusSaved());
                } else if (isBlock){
                    dispatch(duplicateMetaFieldBlock(boxId, fieldId, blockId));
                    dispatch(setStatusSaved());
                }

                break;

            case "control+F":
            case "command+F":
                if(isBox){
                    dispatch(createField(boxId));
                    dispatch(setStatusSaved());
                } else if (isField && hasChildren){
                    dispatch(createField(boxId, fieldId));
                    dispatch(setStatusSaved());
                }

                break;

            case "control+L":
            case "command+L":
                setLegendModalVisible(true);

                break;

            case "control+O":
            case "command+O":
                if((isField && canHaveOptions) || isOption){
                    dispatch(createOption(boxId, fieldId));
                    dispatch(setStatusSaved());
                }

                break;

            case "control+R":
            case "command+R":
                if(isField){
                    dispatch(toggleFieldIsRequired(fieldId, boxId, true));
                }

                break;

            case "control+S":
            case "command+S":
                dispatch(metaSubmit(values));
                dispatch(setStatusSaved());

                break;

            case "control+del":
            case "command+del":
                if(isBox){
                    dispatch(deleteBox(boxId));
                    dispatch(setStatusSaved());
                } else if (isField){
                    dispatch(deleteField(boxId, fieldId));
                    dispatch(setStatusSaved());
                } else if (isOption){
                    dispatch(deleteOption(boxId, fieldId, optionId));
                    dispatch(setStatusSaved());
                } else if (isVisibilityCondition) {
                    dispatch(deleteVisibilityCondition(boxId, fieldId, visibilityConditionId));
                    dispatch(setStatusSaved());
                } else if (isBlock) {
                    dispatch(deleteFieldBlock(boxId, fieldId, blockId));
                    dispatch(setStatusSaved());
                }

                dispatch(deselectElements());

                break;

            case "control+shift+A":
            case "command+shift+A":
                if(isField){
                    dispatch(toggleFieldShowInArchive(fieldId, boxId, false));
                }

                break;

            case 'control+shift+B':
            case 'command+shift+B':
                if(isField && field.type === FLEXIBLE){
                    dispatch(createFieldBlock(boxId, fieldId));
                }

                break;

            case "control+shift+R":
            case "command+shift+R":
                if(isField){
                    dispatch(toggleFieldIsRequired(fieldId, boxId, false));
                }

                break;

            case 'control+V':
            case 'command+V':
                if(isField && field.activeTab && field.activeTab === 2){
                    dispatch(createVisibilityCondition(boxId, fieldId));
                }

                break;

            case "control+shift+del":
            case "command+shift+del":
                dispatch(deleteAllMeta(belongsTo, find));
                dispatch(setStatusSaved());
                dispatch(deselectElements());

                break;

            case "control+shift+V":
            case "command+shift+V":
                const newView = (view === 'tabular') ? 'list' : 'tabular';
                handleSwitchView(newView);

                break;
        }
    };

    return (
        <Hotkeys
            keyName={metaHotKeys.keysMap()}
            onKeyDown={handleKeyPressEvent}
        >
            <KeyboardLegendModal
                modalVisible={legendModalVisible}
                setModalVisible={setLegendModalVisible}
            />
            <a
                href="#"
                className="keyboard-legend-link"
                onClick={e => {
                    e.preventDefault();
                    setLegendModalVisible(!legendModalVisible);
                }}
            >
                <Icon icon="bx:bx-help-circle" width="18px"/>
                <span>
                    {useTranslation("Keyboard legend")}
                </span>
            </a>
            {boxes.length > 0
                ? (
                    <React.Fragment>
                        {modalsContent}
                        <div className="acpt-meta-wrapper">
                            <MiniNavMap values={values} />
                            <div className="acpt-meta-list-wrapper">
                                <div className="space-between mb-3 flex-center">
                                    <div className="acpt-meta-view-switcher">
                                        <Tippy title={useTranslation("List view")}>
                                            <a
                                                href="#"
                                                onClick={e => {
                                                    e.preventDefault();
                                                    handleSwitchView("list");
                                                }}
                                                className={view === 'list' ? 'active' : ''}
                                            >
                                                <Icon icon="bx:list-ul" width="18px" />
                                            </a>
                                        </Tippy>
                                        <Tippy title={useTranslation("Tabular view")}>
                                            <a
                                                href="#"
                                                onClick={e => {
                                                    e.preventDefault();
                                                    handleSwitchView("tabular");
                                                }}
                                                className={view === 'tabular' ? 'active' : ''}
                                                title={useTranslation("Tabular view")}
                                            >
                                                <Icon icon="bx:table" width="18px" />
                                            </a>
                                        </Tippy>
                                    </div>
                                    <a
                                        href="#"
                                        onClick={(e)=>{
                                            e.preventDefault();
                                            showHideAllFields();
                                        }}
                                    >
                                        {useTranslation("Show/hide all fields")}
                                    </a>
                                </div>
                                <VerticalSortableList
                                    items={boxes}
                                    onSortEnd={onSortEnd}
                                    useDragHandle
                                    lockAxis="y"
                                    helperClass="dragging-helper-class"
                                    disableAutoscroll={false}
                                    useWindowAsScrollContainer={true}
                                />
                            </div>
                        </div>
                    </React.Fragment>
                )
                : (
                    <React.Fragment>
                        <div className="acpt-alert acpt-alert-warning">
                            {useTranslation('No meta box already created. Create the first one now by clicking the button "Add meta box"!')}
                        </div>
                    </React.Fragment>
                )
            }
        </Hotkeys>
    );
};

export default Meta;