import React from "react";
import useTranslation from "../../../../hooks/useTranslation";

const KeyboardLegendGroup = ({group}) => {

    return (
        <div className="keys-group">
            <h3>{useTranslation(group.title)}</h3>
            <ul>
                {group.keys && group.keys.map((key) => {
                    return (
                        <li>
                            <span className="keys">
                                {key.keys && key.keys.map((k)=>{
                                    return (
                                        <span className={`${k !== '+' ? 'key' : ''}`}>
                                            {useTranslation(k)}
                                        </span>
                                    );
                                })}
                            </span>
                            <span className="legend">
                                {useTranslation(key.legend)}
                            </span>
                        </li>
                    );
                })}
            </ul>
        </div>
    );
};

export default KeyboardLegendGroup;